import React from 'react';
import { HashRouter as Router, Route, Link } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import Home from '../home';
import Contact from '../contact';

import './menu.css';

const history = createBrowserHistory();

const Menu = () => (
    <div className="error-page">
        <h2 className="error-name">Page not found</h2>
    </div>
)

const Catering = () => (
    <div className="error-page">
        <h2 className="error-name">Page not found</h2>
    </div>
)

const AboutUs = () => (
    <div className="error-page">
        <h2 className="error-name">Page not found</h2>
    </div>
)

class Navigation extends React.Component {
    render() {
        return (
            <Router history={history} >
                <nav className="menu">
                    <li className="menu__item">
                        <Link to="/menu" className="menu__link link">Menu</Link>
                    </li>
                    <li className="menu__item">
                        <Link to="/catering" className="menu__link link">Catering</Link> 
                    </li>
                    <li className="menu__item">
                        <Link to="/about_us" className="menu__link link">About Us</Link>
                    </li>
                    <li className="menu__item">
                        <Link to="/contact" className="menu__link link">Contact</Link>
                    </li>
                </nav>

                <Route exact path="/" component={Home} />
                <Route path="/menu" component={Menu} />
                <Route path="/catering" component={Catering} />
                <Route path="/about_us" component={AboutUs} />
                <Route path="/contact" component={Contact} />
            </Router>
        )
    }
}

export default Navigation;