import React from 'react';
import axios from 'axios';

import './contact.css';

export default class ContactList extends React.Component {
    state = {
        name: '',
        email: '',
        comment: ''
    }

    onChangeName = event => {
        this.setState({ 
            name: event.target.value,
        });
    }

    onChangeEmail = event => {
        this.setState({
            email: event.target.value,
        });
    }

    onChangeComment = event => {
        this.setState({
            comment: event.target.value,
        });
    }

    handleSubmit = event => {
        event.preventDefault();

        axios.post(`https://formula-test-api.herokuapp.com/contact`, {
            name: this.state.name,
            email: this.state.email,
            comment: this.state.comment 
        }).then(res => {
                console.log(res);
            })
    }

    render() {
        return (
            <div className="form-box">
                <form onSubmit={this.handleSubmit} className="contact-form">
                    <li className="form">
                        <label className="form__text">
                            Name:
                        </label>
                        <input type="text" name="name" onChange={this.onChangeName} className="form__input" />
                    </li>
                    <li className="form">
                        <label className="form__text">
                            Email:
                        </label>
                        <input type="email" name="email" onChange={this.onChangeEmail} className="form__input" />
                    </li>
                    <li className="form">
                        <label className="form__text">
                            Comment:
                        </label>
                        <textarea type="text" name="comment" onChange={this.onChangeComment} className="form__textarea" rows="6" cols="30"></textarea>
                    </li>
                    <li className="form">
                        <button type="submit" className="form__button">Send</button>
                    </li>
                </form>
            </div>
        )
    }
}