import React, {Component} from 'react';
import axios from 'axios';

import './cards.css';

export default class Cards extends Component {

    state = {
        food: []
    }

    componentDidMount() {
        // axios.get(`https://formula-test-api.herokuapp.com/menu`)
        axios.get('https://api.myjson.com/bins/1cj9v1')
            .then(
                res => {
                    return this.setState({ food: res.data })
                })
            .catch(error => console.log(error))
    }

    filterByExpiration = () => {
        const goodFood = this.state.food.filter(food => {
            return Math.floor(Date.parse(new Date(food.expirationDate)) / 1000) > Math.floor(Date.now() / 1000);
        })

        return goodFood;
    };

    render() {

        return (
            <div className="cards-block">
                {this.filterByExpiration().map((food) => 
                    <div className="card" key={food.id}>
                        <div className="box-image" style={{ backgroundImage: 'url(' +  food.backgroundURL + ')' }}></div>
                        <div className="box box--other">
                            <div className="card__title">{food.name}</div>
                            <div className="card__description">{food.description}</div>
                        </div>
                    </div>
                )}
            </div>
        )
    }
}